* {
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

.nav-Container {
    background: rgb(243, 234, 63);
    display: grid;
    grid-template-columns: 1rem 2fr 2fr 2rem;
}

.logo {
    color: black;
    width: 2rem;
    margin: 0.6rem 0.8rem;
    cursor: pointer;
}

.tit {
    text-decoration: none;
    color: black;
    margin: auto;
}

.nav-Container li {
    display: flex;
    justify-content: space-around;
}

.l-list {
    grid-column: 2/3;
    display: flex;
}

.navhead {
    display: flex;
    /* border: 3px solid red; */
    margin-bottom: 0.5rem;
}

.navhead p {
    font-size: 1.5rem;
    margin: auto;
    cursor: pointer;
}

.title {
    display: flex;
}

.r-list {
    /* border: 3px solid rgb(60, 0, 0); */
    grid-column: 3/4;
    display: flex;
    justify-content: space-around;
    justify-self: end;
    margin: 0.3rem 0;
}

.r-list button a {
    text-decoration: none;
    background: none;
    color: #000000;
}

.r-list button {
    background: rgb(250, 250, 150);
    font-size: 1.03rem;
    width: 8rem;
    margin: 0rem 0.8rem;
    cursor: pointer;
    border: none;
}

.r-list button:hover {
    background: #ffffdb;
}

.r-list input {
    width: 15rem;
    margin: 0rem 0.3rem;
    font-size: 1.3rem;
    padding: 0px 10px;
    border: 2px solid rgb(0, 0, 0);
    background: rgb(131, 131, 131);

}

.logout button {
    height: 3rem;
}



.burger {
    grid-column: 3/4;
    height: 30px;
    width: 50px;
    background: none;
    justify-self: end;
    margin-top: 0.2rem;
    cursor: pointer;
    display: none;
}

.line {
    height: 4px;
    width: 45px;
    margin: 2px auto;
    background: rgb(161, 168, 30);
}


@media(max-width:1100px) {
    .r-list {
        grid-column: 3/4;
        display: flex;
        justify-content: space-around;
        justify-self: end;
    }

    .r-list button {
        /* width: 3.8rem; */
        margin: 0rem 0.7rem;
    }

    .r-list input {
        width: 13rem;
        margin: 0rem 0.2rem;
    }
}

@media(max-width:830px) {
    .burger {
        display: block;
    }

    .nav-Container {
        display: grid;
        grid-template-columns: 2fr 2fr;
    }

    .l-list {
        grid-column: 1/4;
        display: grid;
        /* background: rgb(163, 142, 142); */
        height: 4rem;
        box-shadow: 10px 10px 10px rgb(182, 184, 54);
    }

    .navhead {
        display: grid;
        grid-template-columns: 2fr 1fr;
    }

    .logo {
        /* border: 2px solid firebrick; */
        margin-top: 0.5rem;
        height: 3rem;
    }

    .title p {
        /* display: grid;
        justify-self: start; */
        margin-left: -0.5rem;
        /* border: 2px solid rebeccapurple; */
    }

    .tit {
        margin-left: -0.5rem;
    }

    .burger {
        /* background: rgb(115, 96, 96); */
        justify-self: end;
        margin: auto 0.3rem;
    }

    .r-list {
        grid-column: -1/-4;
        margin: auto;
        display: flex;
        flex-direction: column;
        height: 14rem;
        /* border: 2px solid rebeccapurple; */
        /* background: rgb(188, 63, 63); */
        margin: 0.5rem auto;
        transition: all 0.19s ease-out;
    }

    .r-list button {
        height: 2rem;
        width: 15rem;
        margin: 0.3rem;
        border: 1px solid black;
    }

    .r-list input {
        width: 13.5rem;
        height: 2rem;
        margin: 0.3rem;
        border: 1px solid black;
    }
}


.hide {
    opacity: -1;
    height: 0px;
}

.g {
    background: #000;
}

@media (max-width:949px){
    .logout{
        display: flex;
        flex-direction: row;
    }
}
@media (max-width:830px){
    .logout{
        display: flex;
        flex-direction: column;
    }
}
* {
    margin: 0;
    padding: 0;
}

.container h1 {
    margin-bottom: 1.5rem;
    text-align: center;
}

.container {
    display: grid;
    justify-content: center;
    /* background: #000; */
    color: rgb(0, 0, 0);
    padding: 3rem 0;
}

.title {
    margin-top: 0.5rem;
    height: 2.6rem;
    padding: 0 0.5rem;
    font-size: 1.4rem;
}

.ntitle {
    height: 2.3rem;
    font-size: 1.3rem;
    padding: 0 0.3rem;
    border-radius: 0.4rem 0.4rem;
    text-transform: capitalize;
    border: none;
    color: rgb(0, 0, 0);
    background: none;
    border-bottom: 1px solid rgb(0, 0, 0);
}

/* .container input{
    border: 2px solid black;
} */
.container button {
    /* border: 2px solid black; */
    height: 2.5rem;
    width: 12rem;
    border-radius: 0.4rem;
    font-size: 1.3rem;
    margin-bottom: 2rem;
    cursor: pointer;
    background: none;
    color: rgb(0, 0, 0);
    border: 1px solid rgb(0, 0, 0);
}

#note {
    display: grid;
    border-radius: 0.4rem 0.4rem;
    height: 15rem;
    width: 60rem;
    resize: none;
    margin: 2rem 0;
    padding: 0.5rem 0.3rem;
    font-size: 1.5rem;
    border: none;
    background: none;
    border-bottom: 1px solid rgb(0, 0, 0);
   color: rgb(0, 0, 0);
    text-transform: capitalize;
}



.note-container {
    padding: 2rem 1rem;
    /* background: rgb(0, 0, 0); */
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    color: rgb(0, 0, 0);
}


.card {
    border:1px solid red;
    border:none;
    border-bottom: 1px solid rgb(0, 0, 0);
    width: 25rem;
    text-transform: capitalize;
    /* margin: 1rem; */
    margin: 0.6rem 0.5rem;
    justify-self: center;
    height: 10rem;
    padding: 0.3rem 0.4rem;
    display: grid;
    /* overflow:auto; */
    /* overflow-wrap: break-word; */
    grid-template-rows: 0.7fr 2fr 0.8fr;
}

.ctitle{
    /* overflow-wrap: break-word; */
    overflow: hidden;
}

.card h1{
    /* color: red;
    border: 1px solid red; */
    display: flex;
    overflow: hidden;
    /* border: 1px solid white; */
    width: 22rem;
    /* overflow-wrap: break-word; */
    /* white-space: initial; */
}

.card p {
    font-size: 1.4rem;
    /* overflow: scroll; */
    overflow-x: hidden;
    overflow-y: auto;
    position: relative;
    

}

.card p::-webkit-scrollbar {
    height: 4px;              /* height of horizontal scrollbar ← You're missing this */
    width: 2px;    
    background: #000;          /* width of vertical scrollbar */
    /* border: 1px solid #d5d5d5; */
  }
.card-top{
    display: grid;
    grid-template-columns: 1fr 0.5fr;
    text-align: start;
    /* overflow-wrap: break-word; */
}
.card-top .dlt{
    /* border: 1px solid red; */
    width: 3rem;
    display: grid;
    justify-self: end;
}

.card button {
    width: 12rem;
    /* margin: 0.5rem 0.9rem; */
    background: none;
    border: none;
    cursor: pointer;
    display: flex;
    color: rgb(0, 0, 0);
    /* justify-content: end; */
    margin: 0.2rem 0.5rem;
    /* border: 2px,solid greenyellow; */
}


